.grid {
    padding: 48px 75px 0 75px;

    @media (max-width: 1020px) {
        & {
            padding: 48px 20px 0 20px;
        }
    }

    .titleContainer {
        display: flex;
        align-items: flex-start;
        margin-bottom: 8px;

        .title {
            font-family: TT Commons;
            font-size: 40px;
            color: #FEFEFE;
            margin: 0;
        }
        .badge {
            border-radius: 4px;
            background-image: linear-gradient(to right, #7E0188, #400146);
            padding: 2px 6px 2px 6px;
            width: fit-content;
            color: #FEFEFE;
            font-size: 14px;
            font-weight: bold;
            margin-left: 10px;
            margin-top: 5px;
        }
    }
    
    .subTitle {
        font-family: Hind;
        font-style: normal;
        font-weight: normal;
        font-size: 22px;
        line-height: 120%;
        color: #F7F9FD;
        margin-bottom: 24px;
    }

    .carousel {
        position: relative;
    }
}
