.card {
    background-size: cover;
    background-blend-mode: normal, multiply, normal;
    box-shadow: 4px 4px 12px rgba(28, 34, 46, 0.03), 4px 4px 12px rgba(28, 34, 46, 0.05);
    border-radius: 8px;
    padding: 15px 15px 10px 15px;
    font-family: Hind;
    color: #FEFEFE;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    display: flex;
    flex-direction: column;

    &:hover {
        transform: scale(1.05);
    }

    .hide {
        opacity: 0;
    }

    .cardTitle {
        font-size: 24px;
        font-weight: 600;
        font-family: 'TT Commons';
        margin: 0px;
    }

    .tutor {        
        width: fit-content;
        height: 38px;
        order: 0;
        flex-grow: 0;
        flex: none;
        margin: 10px 0px 15px 0px;
       
        .tutorBackground{
            background: rgba(255, 255, 255, 0.2);
            border-radius: 24px;
            padding: 0 12px 0 4px;
            display: flex;
            flex-direction: row;
            align-items: center;    
        }
        
        .tutorAvatar {
            width: 34px;
            height: 34px;
            border-radius: 80px;
            margin-right: 8px;
            background: center / cover no-repeat;
        }

        .tutorName {
            font-family: 'Hind';
            font-style: normal;
            font-weight: 600;
            font-size: 18px;
            color: #FFFFFF;
        }

        .learnWith {
            font-weight: 700;
            font-size: 14px;
            color: #FFFFFF;
            margin-right: 6px;
            opacity: 0.5;
        }
    }

    .cardSubTitle {
        font-size: 16px;
        transition: opacity 0.2s ease-in;
        margin-bottom: 20px;
    }

    .cardActions {
        margin-top: auto;
        padding-top: 30px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        align-content: center;
        font-size: 14px;
        transition: opacity 0.2s ease-in;
        text-align: center;

        .cardList {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            align-content: center;
            gap: 20px;

            .childEl {
                position: relative;
                &:not(:last-child)::after {
                    position: absolute;
                    content: '.';
                    left: 114%;
                    top: 25%;
                    transform: translate(-50%,-50%);
                    z-index: 1;
                    pointer-events: none;
                    font-size: 30px;
                    color: #7B8292;
                }

                .cardActionType {
                    height: 10px;
                    margin-right: 5px;
                }

                .cardActionNumber {
                    font-weight: bold;
                }
            }
        }
    }

    .goToChannel {
        font-size: 14px !important;
        height: 32px;
        width: 145px !important;

        &.comingSoon {
            filter: brightness(0.5);
        }

        &:only-child {
            margin-left: auto;
        }
    }
}
