.sliderBack {
    position: absolute;
    top: calc(50% - 42px);
    left: -24px;
    transform: rotate(180deg);

    width: 46px;
    height: 46px;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 5px;

    background: rgba(255, 255, 255, 0.8);
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
    border-radius: 24px;
    border: 0;

    fill: #05112A;

    &:disabled {
        opacity: 0.5;
    }

    @media (max-width: 1020px) {
        left: -5px;
    }
}

.sliderNext {
    position: absolute;
    top: calc(50% - 42px);
    right: -24px;

    width: 46px;
    height: 46px;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 5px;

    background: rgba(255, 255, 255, 0.8);
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
    border-radius: 24px;
    border: 0;

    &:disabled {
        opacity: 0.5;
    }

    @media (max-width: 1020px) {
        right: -5px;
    }
}

.dotGroup {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    gap: 8px;

    :global(.carousel__dot) {
        height: 12px;
        width: 12px;
        border-radius: 50%;
        border: 0;
        opacity: 0.3;
        background-color: #C4C4C4;
        padding:0px;
    }
    :global(.carousel__dot--selected) {
        opacity: 1;
    }
}

.gridList {
    display: grid;
    grid-auto-rows: 1fr;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 24px;
    grid-row-gap: 24px;

    @media (max-width: 1550px) {
        & {
            grid-template-columns: repeat(3, 1fr);
        }
    }

    @media (max-width: 1350px) {
        & {
            grid-template-columns: repeat(2, 1fr);
        }
    }

    @media (max-width: 800px) {
        & {
            grid-template-columns: repeat(1, 1fr);
        }
    }
}

.sliderContainer {
    :global(.carousel__slide-focus-ring) {
        outline: none !important;
    }
}